import styled from 'styled-components';
import { colors } from '../../utils/colors';
import { device } from '../../utils/layoutBreakpoints';

export const StyledGetDemoFormContainer = styled.div`
  font-size: 0.8rem;
  line-height: 1.1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 15px;
  width: 100%;
  label {
    font-weight: 500;
    display: flex;
    flex-direction: column;
    gap: 5px;
    width: 100%;
  }

  input,
  textarea {
    width: 100%;
    box-sizing: border-box;
    background-color: ${colors.primary.white};
    color: ${colors.primary.black};
    border: 1px solid ${colors.secondary.gray300};
    font-family: 'Rubik';
    border-radius: 5px;
    &::placeholder {
      color: ${colors.primary.gray500};
      font-family: 'Rubik';
    }
    &:focus {
      outline: none;
      border: 1px solid ${colors.primary.blue};
    }
  }

  input {
    height: 36px;
    padding: 10px 15px;
  }

  textarea {
    height: 100px;
    padding: 10px 15px;
    resize: none;
  }

  fieldset {
    margin-bottom: 30px;
    display: flex;
    flex-direction: column;
    width: 100%;
    gap: 10px;
    legend {
      font-weight: 500;
      margin-bottom: 10px;
    }
    label {
      cursor: pointer;
      background: ${colors.primary.white};
      border: 1px solid ${colors.secondary.gray300};
      border-radius: 8px;
      padding: 10px 15px;
      text-align: center;
      box-sizing: border-box;
    }
    input {
      display: none;
      &:checked + label {
        color: ${colors.primary.white};
        background-color: ${colors.primary.blue};
      }
    }

    @media ${device.tablet} {
      flex-direction: row;
    }
  }
`;

export const StyledGetDemoContainer = styled.div`
  width: 100%;
  display: flex;
  background-color: ${colors.secondary.gray100};
  border-radius: 16px;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
  position: relative;
  z-index: 2;
  min-height: 580px;

  & > div:first-child {
    padding: 25px 25px;
    width: 100%;
    box-sizing: border-box;
  }

  @media ${device.tablet} {
    & > div:first-child {
      padding: 50px 50px;
    }
  }
`;

export const GetDemoPageWrapper = styled.div`
  width: 100%;
  min-height: 100vh;
  height: 100%;
`;

export const GetDemoContentContainer = styled.div`
  max-width: 1268px;
  margin: auto;
  padding: 30px 40px 80px;
  height: 100%;
  & > div:first-child {
    width: 100%;
    @media ${device.laptop} {
      width: auto;
    }
  }
`;

export const GetDemoHeroSection = styled.section`
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  flex-direction: column;
  margin-bottom: 50px;

  @media ${device.laptop} {
    margin-bottom: 75px;
    gap: 40px;
    flex-direction: row;
  }
`;

export const HeroInfo = styled.div`
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  gap: 25px;

  @media ${device.tablet} {
    flex-direction: row;
  }

  @media ${device.laptop} {
    width: 90%;
    flex-direction: column;
  }
`;

export const HeroTextContent = styled.div`
  display: flex;
  flex-direction: column;
  gap: 25px;
`;

export const HeroImage = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  div {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  width: 60%;

  @media ${device.laptop} {
    width: 100%;
  }

  @media ${device.laptop} {
    width: 60%;
  }
`;

export const TrialExperience = styled.section`
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  gap: 25px;
  align-items: center;
  margin-top: 50px;

  @media ${device.laptop} {
    margin: 75px 0 50px 0;
    gap: 50px;
    flex-direction: row;
  }
`;

export const TrialExperienceImage = styled.div`
  width: 70%;
`;

export const TrialExperienceTextContent = styled.div`
  display: flex;
  flex-direction: column;
  gap: 25px;
`;

export const TrialExperienceItem = styled.div`
  display: flex;
  align-items: flex-start;
  gap: 10px;
  div {
    width: 25px;
    height: 25px;
  }
  svg {
    width: 25px;
    height: 25px;
  }
`;

export const TrialExperienceList = styled.div`
  display: flex;
  flex-direction: column;
  gap: 15px;

  @media ${device.laptop} {
    gap: 20px;
  }
`;

export const HowItWorksContainer = styled.div`
  margin-bottom: 25px;

  @media ${device.laptop} {
    margin-bottom: 75px;
  }
`;

export const FAQContainer = styled.div`
  padding-top: 25px;
  background-color: ${colors.secondary.gray100};
  margin-bottom: 25px;
`;
