import React, { FC, useState } from 'react';
import { InternalLinks } from '../../../utils/links';
import {
  StyledStepByStep,
  ImagesContent,
  TextContent,
  StepsList,
  Step,
  StepDescription,
} from './styled';
import { Text } from '../Text';
import CustomLink from '../Link';
import { StaticImage } from 'gatsby-plugin-image';

type Step = {
  text: string;
  subText: string;
};

interface Props {
  //   title: string;
  //   description: string;
}

const steps: Step[] = [
  {
    text: 'You must be a Campaign Service Provider (CSP) with The Campaign Registry(TCR).',
    subText:
      'As a CSP you will create a Brand(s), create Campaign(s), and select Telgorithm as your Connectivity Partner in TCR.',
  },
  {
    text: 'Integrate with Telgorithm.',
    subText:
      'Telgorithm will automatically import your approved Campaigns into your Telgorithm account. You can now purchase numbers.',
  },
  {
    text: 'Fund your account & start sending & receiving messages.',
    subText:
      'Test inbound and outbound text messages, get any questions answered by our technical or support teams, and experience the Telgorithm magic.',
  },
];

const StepByStep: FC<Props> = () => {
  const [highlightedImage, setHighlightedImage] = useState(0);
  const [expandedStep, setExpandedStep] = useState(0);

  return (
    <StyledStepByStep>
      <TextContent>
        <Text.Title.Bold.lg>How it works:</Text.Title.Bold.lg>
        <StepsList expandedStep={expandedStep}>
          {steps.map((step, index) => (
            <Step
              onMouseEnter={() => setHighlightedImage(index + 1)}
              onMouseLeave={() => setHighlightedImage(0)}
              onClick={() => setExpandedStep(st => st === index + 1 ? 0 : index + 1 )}
            >
              <Text.Regular.Bold.lg>{index + 1 + '. ' + step.text}</Text.Regular.Bold.lg>
              <StepDescription>
                <Text.Regular.Normal.sm>{step.subText}</Text.Regular.Normal.sm>
              </StepDescription>
            </Step>
          ))}
        </StepsList>
        <CustomLink address={InternalLinks.GettingStarted} arrow>
          Learn more about integration
        </CustomLink>
      </TextContent>
      <ImagesContent highlightedImage={highlightedImage || expandedStep}>
        <StaticImage
          src="../../../images/Common/CreateBrand.png"
          alt="Create brand"
          objectFit="contain"
        ></StaticImage>
        <StaticImage
          src="../../../images/Common/Campaign.png"
          alt="Campaign"
          objectFit="contain"
        ></StaticImage>
        <StaticImage
          src="../../../images/Common/Messages.png"
          alt="Messages"
          objectFit="contain"
        ></StaticImage>
      </ImagesContent>
    </StyledStepByStep>
  );
};

export default StepByStep;
